/* Footer Styles */
.footer {
  background: #020617; /* Dark Slate */
  padding: 5rem 0 2rem;
  border-top: 1px solid #1e293b;
  color: #94a3b8;
}

.footer-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 4rem;
  margin-bottom: 4rem;
}

.footer-logo {
  font-family: var(--font-heading);
  font-size: 1.5rem;
  font-weight: 800;
  color: #f8fafc; /* Light text for logo */
  margin-bottom: 1.5rem;
  display: inline-block;
  text-decoration: none;
}

.logo-highlight {
  color: var(--primary-color);
}

.footer-desc {
  margin-bottom: 2rem;
  line-height: 1.6;
  font-size: 0.95rem;
  color: #94a3b8;
}

.social-links {
  display: flex;
  gap: 1rem;
}

.social-link {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.05);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #cbd5e1;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.social-link:hover {
  background: var(--primary-color);
  color: white;
  transform: translateY(-3px);
  border-color: var(--primary-color);
}

.footer-heading {
  color: #f8fafc;
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
  font-weight: 600;
}

.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-links li {
  margin-bottom: 0.8rem;
}

.footer-links a {
  color: #94a3b8;
  text-decoration: none;
  transition: all 0.2s ease;
  font-size: 0.95rem;
}

.footer-links a:hover {
  color: var(--primary-color);
  padding-left: 5px;
}

.footer-bottom {
  padding-top: 2rem;
  border-top: 1px solid #1e293b;
  font-size: 0.9rem;
  color: #64748b;
}

.footer-bottom-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.compliance-text {
  text-align: right;
}

/* Responsive */
@media (max-width: 992px) {
  .footer-grid {
    grid-template-columns: 1fr 1fr;
    gap: 3rem;
  }
}

@media (max-width: 576px) {
  .footer-grid {
    grid-template-columns: 1fr;
    gap: 2.5rem;
  }

  .footer-bottom-content {
    flex-direction: column;
    text-align: center;
  }
  
  .compliance-text {
    text-align: center;
  }
}