/* Header Styles */
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  transition: all 0.4s ease;
  padding: 1.5rem 0;
  background: transparent;
}

.header.scrolled {
  background: var(--bg-glass);
  backdrop-filter: blur(12px);
  padding: 1rem 0;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid var(--bg-glass-border);
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-family: var(--font-heading);
  font-size: 1.5rem;
  font-weight: 800;
  color: var(--text-main);
  letter-spacing: -0.5px;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  z-index: 1002; /* Above mobile menu */
}

.logo-highlight {
  color: var(--primary-color);
}

/* Navigation */
.nav-menu {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.nav-list {
  display: flex;
  align-items: center;
  gap: 2.5rem;
}

.nav-link {
  font-weight: 500;
  color: var(--text-light);
  position: relative;
  padding: 0.5rem 0;
  font-size: 0.95rem;
  transition: color 0.3s ease;
}

.nav-link:hover,
.nav-link.active {
  color: var(--text-main);
}

/* Header Actions */
.header-actions {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.nav-link-text {
  font-weight: 600;
  color: var(--text-main);
  font-size: 0.95rem;
  transition: color 0.3s ease;
}

.nav-link-text:hover {
  color: var(--primary-color);
}

/* Mobile Menu Toggle */
.nav-toggle {
  display: none;
  flex-direction: column;
  gap: 6px;
  cursor: pointer;
  z-index: 2000;
  position: relative;
  background: none;
  border: none;
  padding: 0;
}

.bar {
  width: 26px;
  height: 2px;
  background-color: var(--text-main);
  border-radius: 2px;
  transition: all 0.3s ease;
  box-shadow: 0 0 2px rgba(0,0,0,0.5);
}

.mobile-only {
  display: none;
}

/* Mobile Responsive */
@media (max-width: 992px) {
  .nav-toggle {
    display: flex;
  }

  .desktop-only {
    display: none;
  }

  .nav-menu {
    position: fixed;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100vh;
    background: var(--bg-body);
    padding: 6rem 2rem;
    transition: right 0.4s cubic-bezier(0.77, 0, 0.175, 1);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 3rem;
  }

  .nav-menu.open {
    right: 0;
  }

  .nav-list {
    flex-direction: column;
    align-items: center;
    gap: 2rem;
  }
  
  .nav-link {
    font-size: 1.5rem;
  }

  .mobile-only {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
    width: 100%;
  }

  .nav-toggle.open .bar:nth-child(1) {
    transform: rotate(45deg) translate(5px, 6px);
  }

  .nav-toggle.open .bar:nth-child(2) {
    opacity: 0;
  }

  .nav-toggle.open .bar:nth-child(3) {
    transform: rotate(-45deg) translate(5px, -6px);
  }
}
