/* Services Page Styles */
.services-grid-detailed {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2.5rem;
  margin-bottom: 5rem;
}

.service-card-detailed {
  background: var(--bg-card);
  border: 1px solid var(--bg-glass-border);
  border-radius: var(--radius-lg);
  padding: 3rem;
  transition: var(--transition-normal);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.service-card-detailed:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-lg);
  border-color: rgba(59, 130, 246, 0.3);
}

.service-card-detailed::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background: var(--gradient-primary);
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.4s ease;
}

.service-card-detailed:hover::before {
  transform: scaleX(1);
}

.service-icon-wrapper {
  width: 70px;
  height: 70px;
  background: rgba(59, 130, 246, 0.1);
  border-radius: var(--radius-md);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: var(--primary-color);
  margin-bottom: 2rem;
  transition: var(--transition-normal);
}

.service-card-detailed:hover .service-icon-wrapper {
  background: var(--primary-color);
  color: white;
  transform: rotate(5deg);
}

.service-card-detailed h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.service-features {
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 1px solid var(--bg-glass-border);
}

.service-features li {
  position: relative;
  padding-left: 1.5rem;
  margin-bottom: 0.75rem;
  color: var(--text-light);
  font-size: 0.95rem;
}

.service-features li::before {
  content: 'â–¹';
  position: absolute;
  left: 0;
  color: var(--primary-color);
}

/* Process Page Styles */
.process-timeline {
  max-width: 900px;
  margin: 0 auto;
  position: relative;
}

.process-timeline::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 2px;
  background: var(--bg-glass-border);
  transform: translateX(-50%);
}

.process-step {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6rem;
  position: relative;
}

/* Timeline Connector Dot */
.process-step::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  background: var(--primary-color);
  border: 4px solid var(--bg-body);
  border-radius: 50%;
  box-shadow: 0 0 0 2px var(--primary-color);
  z-index: 2;
}

.process-step:nth-child(even) {
  flex-direction: row-reverse;
}

.step-number {
  font-size: 8rem;
  font-weight: 900;
  color: rgba(255, 255, 255, 0.15);
  position: absolute;
  top: 100px;
  z-index: 0;
  line-height: 1;
  text-align: center;
}

.process-step:nth-child(odd) .step-number {
  left: 70%;
  right: auto;
  text-align: left;
}

.process-step:nth-child(even) .step-number {
  right: 70%;
  left: auto;
  text-align: right;
}

.step-content {
  width: 45%;
  background: var(--bg-card);
  padding: 2.5rem;
  border-radius: var(--radius-lg);
  border: 1px solid var(--bg-glass-border);
  position: relative;
  z-index: 1;
  transition: var(--transition-normal);
}

.step-content:hover {
  transform: translateY(-5px);
  border-color: var(--primary-color);
  box-shadow: var(--shadow-lg);
}

.step-icon {
  font-size: 2.5rem;
  color: var(--primary-color);
  margin-bottom: 1.5rem;
}

/* Careers Page Styles */
.benefits-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-bottom: 6rem;
}

.benefit-card {
  text-align: center;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.02);
  border-radius: var(--radius-md);
  border: 1px solid transparent;
  transition: var(--transition-normal);
}

.benefit-card:hover {
  background: var(--bg-card);
  border-color: var(--bg-glass-border);
}

.benefit-icon {
  font-size: 2.5rem;
  color: var(--secondary-color);
  margin-bottom: 1.5rem;
  transition: var(--transition-normal);
}

.benefit-card:hover .benefit-icon {
  color: var(--primary-color);
  transform: scale(1.1);
}

.jobs-title {
  text-align: center;
  margin-bottom: 3rem;
  font-size: 2.5rem;
}

.jobs-list {
  max-width: 900px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.job-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--bg-card);
  padding: 2rem;
  border-radius: var(--radius-md);
  border: 1px solid var(--bg-glass-border);
  transition: var(--transition-normal);
}

.job-card:hover {
  border-color: var(--primary-color);
  transform: translateX(10px);
}

.job-meta {
  display: flex;
  gap: 1rem;
  color: var(--text-muted);
  font-size: 0.9rem;
  margin-top: 0.5rem;
}

/* Legal Page Styles */
.legal-content {
  max-width: 800px;
  margin: 0 auto;
  background: var(--bg-card);
  padding: 4rem;
  border-radius: var(--radius-lg);
  border: 1px solid var(--bg-glass-border);
}

.legal-content h1 {
  margin-bottom: 1rem;
  font-size: 3rem;
}

.legal-content h2 {
  margin-top: 3rem;
  margin-bottom: 1.5rem;
  color: var(--text-light);
  font-size: 1.5rem;
}

.legal-content ul {
  list-style: disc;
  padding-left: 1.5rem;
  margin-bottom: 1.5rem;
  color: var(--text-muted);
}

.legal-content li {
  margin-bottom: 0.5rem;
}

/* CTA Section (Shared) */
.cta-section {
  margin-top: 6rem;
  text-align: center;
  padding: 5rem 2rem;
  background: linear-gradient(rgba(15, 23, 42, 0.8), rgba(15, 23, 42, 0.8)), url('https://images.unsplash.com/photo-1519389950473-47ba0277781c?ixlib=rb-4.0.3&auto=format&fit=crop&w=1200&q=80');
  background-size: cover;
  background-position: center;
  border-radius: var(--radius-lg);
  position: relative;
  overflow: hidden;
}

.cta-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--gradient-primary);
  opacity: 0.1;
}

.cta-content {
  position: relative;
  z-index: 1;
}

.cta-content h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.cta-content p {
  font-size: 1.25rem;
  color: var(--text-light);
  margin-bottom: 2.5rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .process-timeline::before {
    left: 20px;
  }
  
  .process-step, 
  .process-step:nth-child(even) {
    flex-direction: column;
    align-items: flex-start;
    padding-left: 50px;
    margin-left: 0;
  }

  .process-step::after {
    left: 20px;
  }
  
  .step-number {
    display: block;
    position: relative;
    top: auto;
    left: auto !important;
    right: auto !important;
    font-size: 3rem;
    margin-bottom: 0.5rem;
    opacity: 1;
    color: var(--primary-color);
    width: 100%;
    text-align: left;
  }
  
  .step-content {
    width: 100%;
  }
  
  .job-card {
    flex-direction: column;
    align-items: flex-start;
    gap: 1.5rem;
  }
  
  .job-card .btn {
    width: 100%;
  }
  
  .legal-content {
    padding: 2rem;
  }
}
