/* Home Page Styles */

/* Hero Section */
.hero {
  position: relative;
  padding: 180px 0 100px;
  overflow: hidden;
  background: var(--bg-body);
  min-height: 90vh;
  display: flex;
  align-items: center;
}

/* Animated Background Blobs */
.hero::before, .hero::after {
  content: '';
  position: absolute;
  width: 800px;
  height: 800px;
  border-radius: 50%;
  filter: blur(120px);
  opacity: 0.15; /* Subtle premium feel */
  z-index: 0;
  animation: float 15s infinite ease-in-out;
}

.hero::before {
  background: var(--primary-color);
  top: -200px;
  right: -200px;
  animation-delay: 0s;
}

.hero::after {
  background: var(--secondary-color);
  bottom: -200px;
  left: -200px;
  animation-delay: 7s;
}

@keyframes float {
  0%, 100% { transform: translate(0, 0); }
  50% { transform: translate(40px, 60px); }
}

.hero-content {
  display: grid;
  grid-template-columns: 1fr 1fr; /* 50/50 Split */
  gap: 6rem;
  align-items: center;
  position: relative;
  z-index: 1;
}

.hero-badge {
  display: inline-block;
  padding: 0.5rem 1rem;
  background: rgba(59, 130, 246, 0.1);
  border: 1px solid rgba(59, 130, 246, 0.2);
  border-radius: 50px;
  color: var(--primary-color);
  font-weight: 600;
  font-size: 0.85rem;
  margin-bottom: 2rem;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.hero-title {
  font-size: var(--text-display);
  line-height: 1.1;
  margin-bottom: 1.5rem;
  letter-spacing: -0.03em;
}

.hero-desc {
  font-size: 1.25rem;
  color: var(--text-muted);
  margin-bottom: 3rem;
  max-width: 90%;
  line-height: 1.6;
}

.hero-actions {
  display: flex;
  gap: 1.5rem;
}

.hero-visual {
  position: relative;
  perspective: 1000px;
  display: flex;
  justify-content: center;
}

.hero-card {
  background: var(--bg-card);
  border: 1px solid var(--bg-glass-border);
  border-radius: var(--radius-lg);
  padding: 3rem;
  box-shadow: var(--shadow-card);
  position: relative;
  z-index: 1;
  transform: rotateY(-10deg) rotateX(5deg);
  transition: transform 0.6s cubic-bezier(0.23, 1, 0.32, 1);
  width: 100%;
  max-width: 500px;
}

.hero-card:hover {
  transform: rotateY(0) rotateX(0) scale(1.02);
  box-shadow: var(--shadow-glow);
}

.code-block {
  font-family: 'Fira Code', monospace;
  font-size: 1rem;
  line-height: 1.6;
}

/* Services Section */
.services-section {
  padding: var(--spacing-xl) 0;
  background: var(--bg-body);
  position: relative;
}

.section-header {
  text-align: center;
  max-width: 800px;
  margin: 0 auto 5rem;
}

.section-subtitle {
  color: var(--primary-color);
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 0.85rem;
  display: block;
  margin-bottom: 1rem;
}

.section-title {
  font-size: 3rem;
  margin-bottom: 1.5rem;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2.5rem;
}

.service-card {
  background: var(--bg-card);
  border: 1px solid var(--bg-glass-border);
  border-radius: var(--radius-lg);
  padding: 3rem;
  transition: var(--transition-normal);
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  height: 100%;
  z-index: 1;
}

/* Abstract Shape Background */
.service-card::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 200px;
  height: 200px;
  background: radial-gradient(circle at top right, rgba(59, 130, 246, 0.1), transparent 70%);
  border-radius: 0 0 0 100%;
  transition: var(--transition-normal);
  z-index: -1;
}

.service-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-lg);
  border-color: rgba(59, 130, 246, 0.3);
}

.service-card:hover::after {
  background: radial-gradient(circle at top right, rgba(59, 130, 246, 0.2), transparent 70%);
  width: 300px;
  height: 300px;
}

.service-icon {
  width: 64px;
  height: 64px;
  background: rgba(59, 130, 246, 0.1);
  border-radius: var(--radius-md);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.75rem;
  color: var(--primary-color);
  margin-bottom: 2rem;
  transition: var(--transition-normal);
}

.service-card:hover .service-icon {
  background: var(--primary-color);
  color: white;
  transform: scale(1.1);
}

.service-card h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.service-card p {
  color: var(--text-muted);
  margin-bottom: 2rem;
  flex-grow: 1;
}

.service-link {
  color: var(--primary-color);
  font-weight: 600;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  margin-top: auto;
}

.service-link:hover {
  gap: 0.8rem;
}

/* Tech Stack Grid */
.tech-section {
  padding: 4rem 0;
  background: var(--bg-body);
  border-bottom: 1px solid var(--bg-glass-border);
}

.tech-label {
  text-align: center;
  color: var(--text-muted);
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  margin-bottom: 3rem;
}

.tech-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 3rem;
  max-width: 1000px;
  margin: 0 auto;
}

.tech-category {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.tech-category-title {
  font-size: 1.1rem;
  color: var(--text-light);
  border-bottom: 1px solid var(--bg-glass-border);
  padding-bottom: 0.5rem;
  margin-bottom: 0.5rem;
}

.tech-pills {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
}

.tech-pill {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid var(--bg-glass-border);
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-size: 0.9rem;
  color: var(--text-muted);
  transition: var(--transition-normal);
  cursor: default;
}

.tech-pill:hover {
  background: rgba(59, 130, 246, 0.1);
  border-color: var(--primary-color);
  color: var(--text-main);
  transform: translateY(-2px);
}

/* Process Section */
.process-section {
  padding: var(--spacing-xl) 0;
  background: var(--bg-body);
}

.process-steps {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 3rem;
  position: relative;
  margin-top: 4rem;
}

.process-card {
  text-align: center;
  padding: 3rem 2rem;
  background: var(--bg-card);
  border-radius: var(--radius-lg);
  border: 1px solid rgba(255, 255, 255, 0.03);
  transition: var(--transition-normal);
}

.process-card:hover {
  transform: translateY(-5px);
  border-color: var(--primary-color);
}

.process-number {
  font-size: 5rem;
  font-weight: 900;
  color: rgba(255, 255, 255, 0.03);
  line-height: 1;
  margin-bottom: -2.5rem;
  position: relative;
  z-index: 0;
}

.process-icon-wrapper {
  width: 90px;
  height: 90px;
  background: var(--bg-body);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 2rem;
  border: 2px solid var(--primary-color);
  position: relative;
  z-index: 1;
  font-size: 2.5rem;
  color: var(--primary-color);
  box-shadow: 0 0 20px rgba(99, 102, 241, 0.2);
}

/* Responsive */
@media (max-width: 992px) {
  .hero-content {
    grid-template-columns: 1fr;
    text-align: center;
    gap: 3rem;
  }
  
  .hero-actions {
    justify-content: center;
  }
  
  .hero-desc {
    margin: 0 auto 2.5rem;
  }
  
  .hero {
    padding: 140px 0 80px;
  }
}

@media (max-width: 576px) {
  .hero-title {
    font-size: 2.5rem;
  }
  
  .section-title {
    font-size: 2rem;
  }
}
