@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&family=Outfit:wght@500;700;800&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-main);
  background-color: var(--bg-body);
  color: var(--text-main);
  line-height: 1.7;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-heading);
  font-weight: 700;
  color: var(--text-main);
  margin-bottom: var(--spacing-sm);
  line-height: 1.2;
}

p {
  margin-bottom: 1rem;
}

a {
  text-decoration: none;
  color: inherit;
  transition: var(--transition-fast);
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

/* Utility Classes */
/* Utility Classes */
.agency-container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 24px;
  width: 100%;
}

/* Legacy support for existing components until updated */
.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 24px;
  width: 100%;
}

h1 {
  font-size: var(--text-display);
  letter-spacing: -0.02em; /* Tight tracking */
  line-height: 1.1;
}

body {
  font-size: var(--text-base); /* 1.125rem */
  line-height: 1.6;
}

.section-padding {
  padding: 120px 0 80px;
}

.section-desc {
  max-width: 800px;
  margin: 0 auto;
}

.text-primary { color: var(--primary-color); }
.text-secondary { color: var(--secondary-color); }
.text-accent { color: var(--accent-color); }
.w-100 { width: 100%; }

/* Buttons */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.875rem 1.75rem;
  border-radius: var(--radius-md);
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition-normal);
  border: none;
  font-family: var(--font-main);
  font-size: var(--text-base);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.btn-primary {
  background: var(--gradient-primary);
  color: white;
  box-shadow: var(--shadow-glow);
}

.btn-primary::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(255,255,255,0.2), transparent);
  opacity: 0;
  transition: var(--transition-fast);
  z-index: -1;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 0 30px rgba(99, 102, 241, 0.6);
}

.btn-primary:hover::before {
  opacity: 1;
}

.btn-outline {
  background: transparent;
  border: 2px solid rgba(99, 102, 241, 0.5);
  color: var(--text-main);
}

.btn-outline:hover {
  border-color: var(--primary-color);
  background: rgba(99, 102, 241, 0.1);
  color: white;
}

.text-gradient {
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* About Page Styles */
.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  margin-top: 4rem;
}

.about-heading {
  font-size: 2rem;
  margin-bottom: 1.5rem;
}

.about-desc {
  margin-bottom: 1.5rem;
  line-height: 1.8;
}

.stats-grid {
  display: flex;
  gap: 2rem;
  margin-top: 2rem;
}

.stat-number {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.about-image-wrapper {
  position: relative;
  /* Removed padding to ensure wrapper matches image size */
}

.about-image-border {
  position: absolute;
  top: -20px;
  left: -20px;
  width: 100%;
  height: 100%;
  border: 2px solid var(--primary-color);
  border-radius: var(--radius-lg);
  z-index: 0;
}

.about-image {
  width: 100%;
  border-radius: var(--radius-lg);
  position: relative;
  z-index: 1;
  box-shadow: var(--shadow-lg);
  display: block;
}

/* Contact Page Styles */
.contact-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 4rem;
  max-width: 1000px;
  margin: 0 auto;
}

.contact-item {
  margin-bottom: 2rem;
}

.contact-form {
  background: var(--bg-card);
  padding: 2rem;
  border-radius: var(--radius-md);
  border: 1px solid rgba(255,255,255,0.05);
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: var(--text-light);
}

.form-input {
  width: 100%;
  padding: 0.8rem;
  background: rgba(255,255,255,0.05);
  border: 1px solid rgba(255,255,255,0.1);
  border-radius: var(--radius-sm);
  color: white;
  font-family: inherit;
}

.form-input:focus {
  outline: none;
  border-color: var(--primary-color);
  background: rgba(255,255,255,0.1);
}

/* Scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--bg-body);
}

::-webkit-scrollbar-thumb {
  background: var(--bg-card);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--primary-color);
}

/* Selection Color */
::selection {
  background: var(--primary-color);
  color: white;
}

/* Responsive Media Queries */
@media (max-width: 992px) {
  .about-content {
    grid-template-columns: 1fr;
    gap: 3rem;
    text-align: center;
  }

  .stats-grid {
    justify-content: center;
  }

  .about-image-wrapper {
    max-width: 600px;
    margin: 2rem auto 0;
  }
  
  .contact-grid {
    grid-template-columns: 1fr; /* Force single column */
    gap: 3rem;
  }
}

@media (max-width: 768px) {
  .section-padding {
    padding: 100px 0 60px;
  }

  .section-title {
    font-size: 2.5rem;
  }

  .stats-grid {
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .about-image-border {
    transform: translate(-10px, -10px);
  }
}

@media (max-width: 576px) {
  .container {
    padding: 0 var(--spacing-sm);
  }
  
  .section-title {
    font-size: 2rem;
  }
  
  .about-heading {
    font-size: 1.75rem;
  }
  
  .stat-number {
    font-size: 2rem;
  }
}
