:root {
  /* Premium Agency Palette */
  --bg-body: #0f172a; /* Deep Slate */
  --bg-footer: #020617; /* Darker Slate */
  --bg-card: #1e293b; /* Slate 800 */
  --bg-glass: rgba(15, 23, 42, 0.7);
  --bg-glass-border: rgba(255, 255, 255, 0.08);

  --primary-color: #3b82f6; /* Electric Blue */
  --primary-hover: #2563eb; /* Blue 600 */
  --secondary-color: #64748b; /* Slate 500 */
  
  /* Text Colors */
  --text-main: #f8fafc; /* Off-White */
  --text-muted: #94a3b8; /* Slate 400 */
  --text-light: #cbd5e1; /* Slate 300 */

  /* Gradients */
  --gradient-primary: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  --gradient-glow: radial-gradient(circle at center, rgba(59, 130, 246, 0.15), transparent 70%);

  /* Spacing */
  --spacing-xs: 0.5rem;
  --spacing-sm: 1rem;
  --spacing-md: 2rem;
  --spacing-lg: 4rem;
  --spacing-xl: 8rem;

  /* Borders & Shadows */
  --radius-sm: 4px;
  --radius-md: 8px;
  --radius-lg: 16px;
  --radius-full: 9999px;
  
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-glow: 0 0 20px rgba(59, 130, 246, 0.3);

  /* Typography */
  --font-main: 'Inter', sans-serif;
  --font-heading: 'Inter', sans-serif; /* Using Inter for headings too for a clean tech look */
  
  --text-xs: 0.75rem;
  --text-sm: 0.875rem;
  --text-base: 1.125rem; /* Relaxed body size */
  --text-lg: 1.25rem;
  --text-xl: 1.5rem;
  --text-2xl: 2rem;
  --text-3xl: 3rem;
  --text-4xl: 4rem;
  --text-display: 4.5rem; /* Tight tracking H1 */
  
  /* Transitions */
  --transition-fast: 0.2s ease;
  --transition-normal: 0.3s ease;
}

/* Light Mode Override (Optional - keeping for compatibility but focusing on Dark Mode) */
[data-theme="light"] {
  --bg-body: #ffffff;
  --bg-footer: #f8fafc;
  --bg-card: #f1f5f9;
  --text-main: #0f172a;
  --text-muted: #475569;
}
